o-maxMin-responsive($max-width, $min-height) {\n  @media (max-width: $max-width) and (min-height: $min-height) {\n    @content;\n  }\n}\n\n@mixin combo-minMax-responsive($min-width, $max-height) {\n  @media (min-width: $min-width) and (min-height: $max-height) {\n    @content;\n  }\n}\n\n@mixin responsiveBreakPoint($breakpoint) {\n  @if ($breakpoint == 'smallMobilePort') {\n    @include combo-max-responsive(375px, 800px) {\n      @content;\n    }\n  } @else if ($breakpoint == 'smallMobileLand') {\n    @include combo-max-responsive(800px, 375px) {\n      @content;\n    }\n  }\n  @if ($breakpoint == 'largeMobilePort') {\n    @include combo-max-responsive(425px, 930px) {\n      @content;\n    }\n  } @else if ($breakpoint == 'largeMobileLand') {\n    @include combo-max-responsive(930px, 425px) {\n      @content;\n    }\n  }\n  @if ($breakpoint == 'smallTabPort') {\n    @include combo-max-responsive(800px, 1050px) {\n      @content;\n    }\n  } @else if ($breakpoint == 'smallTabLand') {\n    @include combo-max-responsive(1050px, 800px) {\n      @content;\n    }\n  }\n  @if ($breakpoint == 'largeTabPort') {\n    @include combo-max-responsive(1050px, 1400px) {\n      @content;\n    }\n  } @else if ($breakpoint == 'largeTabLand') {\n    @include combo-max-responsive(1400px, 1050px) {\n      @content;\n    }\n  }\n  @if ($breakpoint == 'desktop') {\n    @include combo-min-responsive(1401px, 1051px) {\n      @content;\n    }\n  } @else if ($breakpoint == 'tv') {\n    @include min-responsive(2500px) {\n      @content;\n    }\n  }\n}\n\n.vue-projects-container {\n  position: relative;\n  height: 100%;\n  width: 60%;\n  display: flex;\n  flex-flow: column nowrap;\n  justify-content: space-between;\n  align-items: center;\n  transform: translate(0, 0) rotate(0);\n  transition: background-color 0.5s, border 0.5s, color 0.5s;\n}\n\n.r__project-link-container {\n  position: absolute;\n  top: 25rem;\n  left: 17rem;\n  height: max-content;\n  width: max-content;\n  padding: 2rem;\n  z-index: 10;\n\n  .r__project-link {\n    height: 4rem;\n    width: max-content;\n    border-radius: 5rem;\n    padding: 1.5rem;\n    text-decoration: none;\n    font-size: 2.4rem;\n    font-family: Roboto Condensed;\n    font-weight: bold;\n    &:first-of-type {\n      margin-right: 2rem;\n    }\n    &:hover {\n      cursor: pointer;\n      transition: color 0.5s, background-color 0.5s;\n    }\n  }\n}\n\n// PROJECT DISPLAY\n\n.vue-project__cover-photo-display {\n  position: relative;\n  height: 55%;\n  width: 90%;\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: center;\n  align-items: center;\n  transition: background-color 0.5s, border 0.5s, color 0.5s;\n}\n\n.project-cover-photo,\n.project-cover-video {\n  position: relative;\n  height: 100%;\n  width: auto;\n}\n\n// PROJECT DESCRIPTION\n.vue-project__description-container {\n  position: relative;\n  height: 45%;\n  width: 90%;\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: space-evenly;\n  align-items: center;\n  padding: 1rem 2rem 0;\n  transition: background-color 0.5s, border 0.5s, color 0.5s;\n}\n\n.project-title {\n  position: absolute;\n  height: max-content;\n  width: max-content;\n  top: 1rem;\n  left: 2rem;\n  font-family: Roboto Slab, Verdana, sans-serif;\n  font-size: 4rem;\n  font-weight: bold;\n\n  @include responsiveBreakPoint('tv') {\n    font-size: 6rem;\n  }\n}\n\n.vue-project-technologies {\n  position: absolute;\n  height: max-content;\n  width: max-content;\n  top: 1rem;\n  right: 2rem;\n  font-family: Roboto Slab, Verdana, sans-serif;\n  font-size: 1.6rem;\n  font-weight: bold;\n  @include responsiveBreakPoint('tv') {\n    font-size: 2rem;\n  }\n}\n\n.vue-project-description-container {\n  position: relative;\n  height: 80%;\n  width: 50%;\n  display: flex;\n  flex-flow: column nowrap;\n  justify-content: flex-start;\n  align-items: flex-start;\n  padding: 6rem 3rem 0 3rem;\n  overflow-y: auto;\n\n  p {\n    font-size: 1.4rem;\n    font-family: Roboto Slab;\n    line-height: 1.5;\n    text-indent: 2rem;\n    margin-bottom: 1rem;\n\n    @include responsiveBreakPoint('tv') {\n      font-size: 2rem;\n    }\n  }\n}\n\n// PROJECT MEDIA\n\n.vue-project-media {\n  position: relative;\n  height: 40%;\n  width: 50%;\n  border-radius: 0.5rem;\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: space-between;\n  align-items: flex-start;\n  transition: background-color 0.5s, border 0.5s, color 0.5s;\n\n  .vue-media-left,\n  .vue-media-right {\n    position: relative;\n    height: 100%;\n    width: 15%;\n    border: none;\n    transition: background-color 0.5s;\n\n    .left-icon,\n    .right-icon {\n      font-size: 3rem;\n      font-weight: bold;\n      transition: color 0.5s;\n    }\n\n    &:hover {\n      cursor: pointer;\n      transition: background-color 0.5s, border 0.5s, color 0.5s;\n      .left-icon,\n      .right-icon {\n        transition: color 0.5s;\n      }\n    }\n  }\n\n  .vue-media-slider {\n    position: relative;\n    height: 100%;\n    width: 70%;\n    display: flex;\n    padding: 0.5rem;\n    justify-content: flex-start;\n    align-content: center;\n    overflow-x: auto;\n    scroll-behavior: smooth;\n\n    .vue-media-option {\n      position: relative;\n      height: 100%;\n      width: max-content;\n      opacity: 0.5;\n      &__video,\n      &__image {\n        position: relative;\n        height: 100%;\n        width: 19rem;\n        margin-right: 0.5rem;\n      }\n\n      &:hover {\n        cursor: pointer;\n        opacity: 1;\n        transition: opacity 0.5s;\n      }\n    }\n    .vue-project-video {\n      position: absoulte;\n      top: 50%;\n      left: 50%;\n      height: 100%;\n      width: auto;\n      transform: translateY(-50%);\n      &:hover {\n        cursor: pointer;\n      }\n    }\n  }\n}\n\n@include responsiveBreakPoint('tv') {\n  .r__vue-projects-container {\n    .r__project-link-container {\n      position: absolute;\n      top: 35rem;\n      left: 15rem;\n      height: max-co