t-weight: bold;\n            &:first-of-type {\n              margin-right: 2rem;\n            }\n            &:hover {\n              cursor: pointer;\n              transition: color 0.5s, background-color 0.5s;\n            }\n          }\n        }\n        .r__vue-project__cover-photo-display {\n          width: 100%;\n          .r__project-cover-photo,\n          .r__project-cover-video {\n            width: 100%;\n          }\n        }\n        .r__vue-project__description-container {\n          width: 100%;\n          flex-flow: column nowrap;\n          .r__vue-project-media {\n            width: 87%;\n            transform: translateY(1rem);\n            margin-bottom: 5rem;\n          }\n\n          .r__project-title {\n            top: 21rem;\n            left: 8rem;\n          }\n\n          .r__vue-project-technologies {\n            top: 23rem;\n            right: 8rem;\n            font-size: 1.2rem;\n          }\n\n          .r__vue-project-description-container {\n            width: 90%;\n            transform: translateY(3rem);\n          }\n        }\n      }\n    }\n  }\n}\n\n// DAY STYLES\n.day {\n  .r__vue-projects-container {\n    .r__project-link-container {\n      .r__project-link {\n        background-color: rgba(#ffd700, 0.8);\n        color: rgba(#333333, 0.8);\n        &:hover {\n          background: #ffd700;\n          color: #333333;\n        }\n      }\n    }\n  }\n  .vue-project__description-container {\n    // border: 0.3rem groove #ffd700;\n    .project-title {\n      color: #333333;\n    }\n    .vue-project-description-container {\n      p {\n        color: #333333;\n      }\n    }\n    .vue-project-technologies {\n      color: #333333;\n    }\n\n    .vue-project-media {\n      border: 0.5rem solid #ffd700;\n      .vue-media-left,\n      .vue-media-right {\n        background-color: rgba(#ffd700, 0.7);\n        .left-icon,\n        .right-icon {\n          color: rgba(#fefefe, 0.7);\n        }\n\n        &:hover {\n          ba